"use client"

import { useState } from "react"
import { Eye, EyeOff } from "lucide-react"

export default function AuthPage() {
  const [isLogin, setIsLogin] = useState(true)
  const [showPassword, setShowPassword] = useState(false)

  return (
    <div className="w-[390px] h-[844px] mx-auto rounded-[40px] bg-white flex flex-col items-center pt-10 shadow-lg overflow-hidden">
      {/* Titlu */}
      <h1
        className={`text-center font-poppins ${
          isLogin ? "font-bold text-[26px]" : "font-semibold text-[26.5px]"
        } bg-gradient-to-b from-[#0C3057] to-[#31649A] bg-clip-text text-transparent mb-5`}
      >
        {isLogin ? "Welcome to Skill Trade" : "Create your account"}
      </h1>

      {isLogin ? (
        <p className="text-center w-[277px] text-black font-poppins text-[15.7px] mb-5">
          Login or Sign up to access your account
        </p>
      ) : (
        <div className="h-5 mb-5" />
      )}

      {/* Tabs Login / Sign Up */}
      <div className="flex w-full">
        <button
          onClick={() => setIsLogin(true)}
          className={`w-1/2 h-12 text-[19px] font-semibold font-poppins rounded-tr-lg transition-all ${
            isLogin ? "bg-[#DCE9FB]" : "bg-white"
          }`}
        >
          Login
        </button>
        <button
          onClick={() => setIsLogin(false)}
          className={`w-1/2 h-12 text-[19px] font-semibold font-poppins rounded-tl-lg transition-all ${
            !isLogin ? "bg-[#DCE9FB]" : "bg-white"
          }`}
        >
          Sign Up
        </button>
      </div>

      {/* Con»õinut formular */}
      <div className="flex flex-col items-center w-full flex-1 bg-[#DCE9FB] rounded-b-[40px] p-5">
        {isLogin ? (
          <>
            {/* Email */}
            <input
              type="email"
              placeholder="Email Address"
              className="w-[323px] h-[49px] rounded-lg border border-[#6F6F6F]/50 bg-white shadow-md px-3 mb-3 text-[15px]"
            />

            {/* Parola + icon */}
            <div className="relative w-[323px] mb-3">
              <input
                type={showPassword ? "text" : "password"}
                placeholder="Password"
                className="w-full h-[49px] rounded-lg border border-[#6F6F6F]/50 bg-white shadow-md px-3 text-[15px]"
              />
              <button
                type="button"
                onClick={() => setShowPassword(!showPassword)}
                className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-600"
              >
                {showPassword ? (
                  <EyeOff className="w-5 h-5" />
                ) : (
                  <Eye className="w-5 h-5" />
                )}
              </button>
            </div>

            {/* Forgot + Login button */}
            <div className="w-[323px]">
              <a
                href="#"
                className="block text-left text-[13px] font-semibold text-black mb-3 hover:underline"
              >
                Forgot password?
              </a>
              <button className="w-full h-[50px] rounded-lg bg-[#6085B9] text-white font-poppins font-bold text-[16px] shadow-md">
                Login
              </button>
            </div>
          </>
        ) : (
          <>
            {/* Sign Up form */}
            <div className="w-[322px] mt-5 space-y-5 relative">
              <div>
                <label className="block text-[#6F6F6F] font-poppins text-[16px] mb-1">
                  Name
                </label>
                <input
                  type="text"
                  className="w-full h-[38px] rounded-lg border border-[#6F6F6F]/50 bg-white shadow-md px-3"
                />
              </div>

              <div>
                <label className="block text-[#6F6F6F] font-poppins text-[16px] mb-1">
                  Email
                </label>
                <input
                  type="email"
                  className="w-full h-[38px] rounded-lg border border-[#6F6F6F]/50 bg-white shadow-md px-3"
                />
              </div>

              <div>
                <label className="block text-[#6F6F6F] font-poppins text-[16px] mb-1">
                  Password
                </label>
                <input
                  type="password"
                  className="w-full h-[38px] rounded-lg border border-[#6F6F6F]/50 bg-white shadow-md px-3"
                />
              </div>

              <div>
                <label className="block text-[#6F6F6F] font-poppins text-[16px] mb-1">
                  Confirm Password
                </label>
                <input
                  type="password"
                  className="w-full h-[38px] rounded-lg border border-[#6F6F6F]/50 bg-white shadow-md px-3"
                />
              </div>

              <button className="w-[288px] h-[46px] rounded-lg bg-[#6085B9] text-white font-poppins font-bold text-[16px] shadow-md absolute left-1/2 -translate-x-1/2 top-[360px]">
                Sign Up
              </button>

              <div className="absolute bottom-[-100px] left-1/2 -translate-x-1/2 text-center text-[#888] font-poppins text-[16px]">
                Have an account?
                <button
                  onClick={() => setIsLogin(true)}
                  className="text-[#6085B9] font-semibold ml-1 hover:underline"
                >
                  SIGN IN
                </button>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  )
}
